.experience{
    flex:2;
    margin: 10px 0 0 80px;
}

.experience .title{
    display: flex;
    text-decoration: underline;
    font-weight: bold;
    font-size: 23px; 
}


.experience .title .image{
    margin-left: -25px;
    width: 25px;
    height: 25px;
}


.experience .exp-title{
    font-weight: bold;
}

.experience .primery-color{
    color: #7B524C;
}

.experience .secondery-color{
    color: #7f8c8d;
}

.experience .subtitle{
    color: black;
    margin-left: 10px;
    font-style: italic;
}

.experience .description{
    width: 50%;
    margin-left: 10px;
}

@media only screen and (max-width: 800px) {
    .experience{
        width: 90%;
        margin: 10px 0 0 30px;
    }

    .experience .description{
        width: 100%;
    }
}